  color: #fed136;
} */

.spacer50px{
  width: 100%;
  height: 50px;
}

.email{
  width: 150px;
  height: auto;
}

.lifeModalDiv{
  margin-bottom: 30px;
  width: 100%;
}

.imageFitPage{
  max-width:60vw;
  max-height: 60vh;
}

@media (max-width: 768px){
  .imageFitPage{
    max-width:60vw;
    max-height: 60vh;
  }
}